// Перепиши функцию makeTransaction() так, 
// чтобы она не использовала callback - функции onSuccess и onError, а принимала всего один параметр transaction и возвращала промис.

const randomIntegerFromInterval = (min, max) => {
  return Math.floor(Math.random() * (max - min + 1) + min);
};

const makeTransaction = (transaction) => {
    const delay = randomIntegerFromInterval(200, 500);
  

    return new Promise((resolve, reject) => {
        
        const canProcess = Math.random() > 0.3;
        
        
        setTimeout(() => {

            
            if (canProcess) {

                const id = transaction.id;
                            
                resolve({id, delay});
            }

            reject(transaction.id);
        }, delay);
       
            
    });
};

const logSuccess = some =>  console.log(`Transaction ${some.id} processed in ${some.delay}ms`);

const logError = id =>  console.warn(`Error processing transaction ${id}. Please try again later.`);



/*
 * Должно работать так
 */
makeTransaction({ id: 70, amount: 150 })
  .then(logSuccess)
  .catch(logError);

makeTransaction({ id: 71, amount: 230 })
  .then(logSuccess)
  .catch(logError);

makeTransaction({ id: 72, amount: 75 })
  .then(logSuccess)
  .catch(logError);

makeTransaction({ id: 73, amount: 100 })
  .then(logSuccess)
  .catch(logError);


